import Authorization from "../user/auth.service";

const base_url = "marketplace-orders";
const MarketplaceOrderService = {
  getList() {
    try {
      return Authorization.checkLogin(`/${base_url}`, { auth: "bearer" })
        .then((res) => {
          if (res.status !== 200) {
            return {
              success: false,
              errors: "Fail check login",
            };
          }

          const list = [];
          for (let item of res.data) {

            const option = {
              id: item.id,
              reference: item.reference,
              status: this.getStatusText(item.status_id),
              cliente: item?.marketplace_user?.email ? item.marketplace_user.email : '',
              order_date: new Date(item.order_date).toLocaleDateString('es-ES', { day: '2-digit', month: '2-digit', year: '2-digit' }),
            };

            list.push(option);
          }

          return list;
        })
        .catch((e) => {
          console.error(e);
        });
    } catch (e) {
      console.error(e);
    }
  },
  getSimpleList() {
    try {
      return Authorization.checkLogin(`/${base_url}`, { auth: "bearer" })
        .then((res) => {
          if (res.status !== 200) {
            return {
              success: false,
              errors: "Fail check login",
            };
          }

          const list = [];
          for (let item of res.data) {
            if (item.id !== 1) {
              const option = {
                value: item.id,
                text: item.alias,
              };

              list.push(option);
            }
          }

          return list;
        })
        .catch((e) => {
          console.error(e);
        });
    } catch (e) {
      console.error(e);
    }
  },

  async getMarketplaceOrder(id) {
    //! Obtiene los datos x un ID
    try {
      const { status, data } = await Authorization.checkLogin(`/${base_url}/${id}`, { auth: "bearer" });
      if (status == 200) {
        return data;
      }
    } catch (e) {
      console.error(e.response);
    }
  },

  async updateMarketplaceOrder(form_data) {
    try {
      const apiStatus = this.getStatusForApi(form_data.status_id);
      const payload = {
        auth: "bearer",
        status: apiStatus
      };
      
      const { status, data } = await Authorization.checkLogin(
        `/${base_url}/update-status/${form_data.id}`, 
        payload, 
        "put"
      );
      
      if (status === 200) {
        return data;
      }
    } catch (e) {
      console.error(e.response);
    }
    return false;
  },

  async delete(ids) {
    const delete_data = {
      auth: "bearer",
      ids,
    };
    try {
      const { status, data } = await Authorization.checkLogin(`/${base_url}/delete`, delete_data, "post");
      if (status == 200) {
        return this.getList().then((list) => {
          return list;
        });
      }
    } catch (e) {
      //! Fail
      console.error(e.response);
    }
  },

  getStatusText(statusId) {
    const status = this.status_list.find(item => item.value === statusId);
    return status ? status.text : 'Desconocido';
  },

  status_list: [
    { value: 'created', text: "Pedido solicidado" },
    { value: 'sended', text: "Enviado" },
    { value: 'completed', text: "Completado" },
    { value: 'cancelled', text: "Cancelado" },
  ],

  statusApiMap: {
    1: "created",     // En el carrito
    2: "sended",      // Pendiente  
    3: "completed",        // Pagado
    4: "cancelled"    // Cancelado
  },

  getStatusForApi(statusId) {
    return this.statusApiMap[statusId] || 'peding';
  },  

};

export default MarketplaceOrderService;
