<template >
    <div>
        <form action="" method="POST" class="mt-2" enctype="multipart/form-data"
        @submit="submitForm">        
        <div class="flex-1 flex-col my-3 px-3 py-2 border-double border border-gray-400 rounded-lg">
            <div class="flex flex-row items-center my-3 mx-2">            
                <label class="uppercase block text-sm font-medium text-gray-700 mx-2 w-32">activo</label>
                <input type="checkbox" class="p-2" v-model="formData.active" >
            </div> 
            <div class="flex flex-row items-center my-3 mx-2">
                <label class="uppercase block text-sm font-medium text-gray-700 mx-2 w-32 ">Destino</label>
                <model-select :options="countries"
                        v-model="formData.country"
                        placeholder="Seleccione Polo *"
                        required
                        class="flex-1 border mt-1 block w-full py-2 px-3 border-gray-400 bg-white rounded-md shadow-sm 
                        focus:outline-none focus:ring-indigo-500 focus:border-indigo-500 sm:text-sm ">
                </model-select> 
            </div>
            <div class="flex flex-row items-center my-3 mx-2">
                <label class="uppercase block text-sm font-medium text-gray-700 mx-2 w-32 ">Título</label>
                <input v-model="formData.title" type="text" class="flex-1 border mt-1 block w-full py-2 px-3 border-gray-400 bg-white rounded-md shadow-sm focus:outline-none focus:ring-indigo-500 focus:border-indigo-500 sm:text-sm ">
            </div> 
            <div class="flex flex-row items-center my-3 mx-2">
                <label class="uppercase block text-sm font-medium text-gray-700 mx-2 w-32 ">Orden</label>
                <input v-model="formData.order" type="text" class="flex-1 border mt-1 block w-full py-2 px-3 border-gray-400 bg-white rounded-md shadow-sm focus:outline-none focus:ring-indigo-500 focus:border-indigo-500 sm:text-sm ">
            </div> 
            <div class="flex flex-row items-center my-3 mx-2">
                <label class="uppercase block text-sm font-medium text-gray-700 mx-2 w-32 ">Precio From</label>
                <input v-model="formData.price_from" type="text" class="flex-1 border mt-1 block w-full py-2 px-3 border-gray-400 bg-white rounded-md shadow-sm focus:outline-none focus:ring-indigo-500 focus:border-indigo-500 sm:text-sm ">
            </div>  
            <div class="flex flex-row items-center my-3 mx-2">
                <label class="uppercase block text-sm font-medium text-gray-700 mx-2 w-32 ">Keyword</label>
                <input v-model="formData.keyword" type="text" class="flex-1 border mt-1 block w-full py-2 px-3 border-gray-400 bg-white rounded-md shadow-sm focus:outline-none focus:ring-indigo-500 focus:border-indigo-500 sm:text-sm ">
            </div>                    
            <div class="flex flex-row items-center my-3 mx-2">
                <label class="uppercase block text-sm font-medium text-gray-700 mx-2 w-32 ">Descripción</label>
                <textarea v-model="formData.description" type="text" class="flex-1 border mt-1 block w-full py-2 px-3 border-gray-400 bg-white rounded-md shadow-sm focus:outline-none focus:ring-indigo-500 focus:border-indigo-500 sm:text-sm ">
                </textarea> 
            </div>   
            <div class="flex flex-row items-center my-3 mx-2">
                <label class="uppercase block text-sm font-medium text-gray-700 mx-2 w-32 ">Info</label>
                <textarea v-model="formData.info" type="text" class="flex-1 border mt-1 block w-full py-2 px-3 border-gray-400 bg-white rounded-md shadow-sm focus:outline-none focus:ring-indigo-500 focus:border-indigo-500 sm:text-sm ">
                </textarea> 
            </div>   
            <!-- <div class="flex flex-col gap-3 my-3 mx-2 w-50">                
                <label class="uppercase block text-sm font-medium text-gray-700">Fichero Imagen </label>
                <div>
                    <input type="file" ref="file" @change="imageSelected" accept="image/*" class="">                    
                </div>
                <figure class="relative w-50 rounded-lg" v-if="image_loaded">
                    <img :src="image_url" class="rounded-lg">                    
                    <span class="absolute image-delete-pos text-red cursor-pointer text-2xl" @click="clearImage"><i class="fas fa-times-circle"></i></span>
                </figure>                
            </div> -->
            <div class="flex flex-row items-center my-3 mx-2">
                <label class="uppercase block text-sm font-medium text-gray-700 mx-2 w-32 rounded-md px-1">Imagen</label>
                <add-images class="flex-1 mt-1 block w-full bg-white focus:outline-none focus:ring-indigo-500 focus:border-indigo-500 sm:text-sm " :index="1" :info="img" v-on:imgStatus="updImage"></add-images>
            </div>               
        </div>
            
        <!-- Button -->
        <div class="flex flex-1 gap-3 mt-10 flex-row-reverse gap-y-4">
                <button type="submit" class="inline-flex border border-transparent h-10 bg-green-600 rounded-md font-semibold text-xs text-white uppercase tracking-widest px-4 py-2 
                    hsover:bg-green-500 focus:outline-none 
                    focus:border-green-700 focus:ring focus:ring-green-200
                    active:bg-green-600 
                    disabled:opacity-25 transition"
                    :disabled="load">
                    Salvar&nbsp;
                    <span :class="{'hidden': !load}"><i class="fa fa-spinner fa-pulse"></i></span>
                    </button> 
                    <button type="button" class="inline-flex border border-gray-400 h-10 bg-red rounded-md font-semibold text-xs text-white uppercase tracking-widest shadow-sm px-4 py-2
                    hover:text-white 
                    focus:outline-none focus:border-red-300 focus:ring focus:ring-red-200 
                    active:text-gray-800 active:bg-red 
                    disabled:opacity-25 transition"
                    :disabled="load"
                    >Cancelar</button> 
                <div v-if="error_message" class="error-message">
                    <i class="fa fa-times-circle"></i>
                    <h5 class="text-xl self-center px-2">
                      <p>{{ error_message_text }}</p>
                    </h5>
                  </div>
                  <div v-if="success_message" class="success-message">
                    <i class="fa fa-check-circle"></i>
                    <h5 class="text-xl self-center px-2">{{ success_message_text }}</h5>
                </div>                           
            </div> 

        </form>
    </div>
</template>
<script>

import CountryService from "@/services/common/country.service";
import OffersService from "@/services/common/offers.service";
import AddImages from '@/components/AddImages';
import moment from 'moment'

import { ModelSelect } from 'vue-search-select'
import 'vue-search-select/dist/VueSearchSelect.css'

export default {
    props: {
        info: {},
        mode: {
            default: 'create'
        }
    }, 
    components: {
        ModelSelect,
        AddImages,
    },   
    name: "CrudOffers",
    data() {
        return {
            error_message_text: '',
            success_message_text: 'La información ha sido enviada satisfactoriamente.',
            error_message: false,
            success_message: false,
            load: false,
            img: {
                id: 0,
                class_name: 'offer',            
                id_object_class: 0,
                subcategory: '',
                lang: 'all',
                image_name: '',
                image_keyword: '',
                url: '',
                file: '',
                ext: '',
                anno: moment().format('YYYY'),
                order: 1,
                active: 1,
            },
            image_loaded: false,
            // image_url: '',
            countries: [],
            formData: {
                id: 0,
                title: '',
                price_from: 0,
                country_id: 0,
                country: {},
                info: '',
                images: [],
                order: 0,
                active: 0,
                keyword: '',
                description: '',
            },
        }
    },
    created() {
        CountryService.getSimpleList().then(p => {
            this.countries = p            
            this.initForm()
        })
    },
    watch: {
        mode() {
            this.initForm()
        },
    },
    computed: {
        is_admin() {
            return Authorization.validateRole()
        }
    },
    methods:{
        initForm() {
            // For upd action
            if(this.info)
                this.clear()
                if(this.mode != 'create') {                
                    OffersService.getOffer(this.info).then(e => {
                        this.formData.id = e.id
                        this.formData.active = e.active
                        this.formData.title = e.title
                        this.formData.price_from = e.price_from
                        this.formData.country_id = e.country_id
                        this.formData.country = e.country_id
                        this.formData.info = e.info
                        this.formData.order = e.order
                        this.formData.keyword = e.keyword
                        this.formData.description = e.description
                        // imagen
                        if(e.image)
                            this.initGallery(e.image)                        
                        
                    })  
                }
        },
        imageSelected(e) {            
            const file = e.target.files[0];
            this.image_url = URL.createObjectURL(file);
            this.image_loaded = true

            // console.log(file)
            // this.formData.image_keyword = file.name.split('.')[0]
            // this.formData.ext = file.name.split('.')[1]
        },
        validate(value) {
            if (value == '')
                return false
            else if(value === undefined)
                return false
            else if(value === null)
                return false
            return true
        },
        
        updImage(obj) {
            if(obj.index >= 0) {
                this.img = obj.img
                this.img.id_object_class = (this.mode == 'create') ? 0 : this.formData.id
            }
        },

        initGallery(images) {
            this.galleries = []
            for(let img of images) {
                const original = img.resources.filter(i => i.image == 'original')                
                this.galleries.push({
                    id: img.id,
                    class_name: img.class_name,            
                    id_object_class: img.id_object_class,
                    subcategory: img.subcategory,
                    lang: img.lang,
                    image_name: img.image_keyword,
                    image_keyword: img.image_keyword,
                    url: original[0].url,
                    file: {},
                    ext: img.ext,
                    anno: img.anno,
                    order: img.order,
                    active: img.active,
                })
            }
            
        },

        submitForm(e) {            
            e.preventDefault();            
            // const selectedFile = this.$refs.file.files[0]
            if(this.validate(this.formData.keyword) && this.validate(this.formData.title) && this.validate(this.formData.country)) {                
                this.load = true
                this.formData.country_id = (typeof this.formData.country == 'object') ? this.formData.country.value : this.formData.country                

                const fd = new FormData()

                // agregando imagen                
                if(this.img.url.includes('blob')) {                    
                    fd.append('images', this.img.file, this.img.file.name)
                } else 
                    fd.append('images', this.img.id)
                fd.append('imgOrder', [1])
                fd.append('subcategory', [this.img.subcategory])

                // agrego los otros campos
                for(let field in this.formData) {
                    fd.append(field, this.formData[field])
                }
                
                OffersService.addUpdOffer({form: fd, id: this.formData.id}, (this.mode == 'create')).then(res => {
                    this.load = false
                    if(res) {
                        this.error_message = false
                        this.success_message = true         
                        setTimeout(() => {
                            this.success_message = false
                        }, 3000); 
                        this.clear()
                    }
                })
                
            } else {
                this.error_message = true
                this.success_message = false
                this.error_message_text = 'Faltan datos por llenar'
                setTimeout(() => {
                    this.error_message = false
                }, 3000); 
            }
        }, 
        /* clearImage(){
            this.image_url = ''
            this.image_loaded = false
            this.$refs.file.value = ''
        }, */   
        clear() {
            this.formDataid = ''
            this.formData.title = ''
            this.formData.price_from = 0
            this.formData.country_id = 0
            this.formData.country = {}
            this.formData.info = ''
            this.formData.order = 0
            this.formData.active = 0
            this.formData.keyword = ''
            this.formData.description = ''
            // this.clearImage()
        }     
    }    
}
</script>
<style scoped>
    .image-delete-pos {
        bottom: -20px;
        right: -19px;
    }
</style>