<template >
    <div>          
        <form action="" class="mt-2" @submit="submitForm">                         
            <div class="flex-1 flex-col my-3 px-3 py-2 border-double border border-gray-400 rounded-lg">
                <div class="flex flex-row items-center my-3 mx-2">
                    <label class="uppercase block text-sm font-medium text-gray-700 mx-2 w-32 ">Active</label>
                    <input v-model="formData.active" type="checkbox" class="border mt-1 py-2 px-3 border-gray-400 bg-white rounded-md shadow-sm focus:outline-none focus:ring-indigo-500 focus:border-indigo-500 sm:text-sm ">            
                </div>                                
                <div class="flex flex-row items-center my-3 mx-2">
                    <label class="uppercase block text-sm font-medium text-gray-700 mx-2 w-32 ">Nombre *</label>
                    <input v-model="formData.alias" type="text" class="flex-1 border mt-1 block w-full py-2 px-3 border-gray-400 bg-white rounded-md shadow-sm focus:outline-none focus:ring-indigo-500 focus:border-indigo-500 sm:text-sm ">
                </div>                                
                <div class="flex flex-row items-center my-3 mx-2">
                    <label class="uppercase block text-sm font-medium text-gray-700 mx-2 w-32 ">Keyword *</label>
                    <input v-model="formData.keyword" type="text" class="flex-1 border mt-1 block w-full py-2 px-3 border-gray-400 bg-white rounded-md shadow-sm focus:outline-none focus:ring-indigo-500 focus:border-indigo-500 sm:text-sm ">
                </div>                                
                <div class="flex flex-row items-center my-3 mx-2">
                    <label class="uppercase block text-sm font-medium text-gray-700 mx-2 w-32 ">Descripción</label>
                    <input v-model="formData.description" type="text" class="flex-1 border mt-1 block w-full py-2 px-3 border-gray-400 bg-white rounded-md shadow-sm focus:outline-none focus:ring-indigo-500 focus:border-indigo-500 sm:text-sm ">
                </div>
                <div class="flex flex-row items-center my-3 mx-2">
                    <label class="uppercase block text-sm font-medium text-gray-700 mx-2 w-32">Categorías *</label>
                    <multi-select
                        :options="categoriesSelected"
                        :selected-options="formData.categories"                        
                        placeholder="Seleccione el estado"
                        @select="onCategorySelect"
                        required                        
                        class="flex-1 border mt-1 block w-full py-2 px-3 border-gray-400 bg-white rounded-md shadow-sm 
                                focus:outline-none focus:ring-indigo-500 focus:border-indigo-500 sm:text-sm ">                        
                    </multi-select>
                </div>                
                <div class="flex flex-row items-center my-3 mx-2">
                    <label class="uppercase block text-sm font-medium text-gray-700 mx-2 w-32 rounded-md px-1">Descuento</label>
                    <input v-model="formData.discount" type="text" class="flex-1 border mt-1 block w-full py-2 px-3 border-gray-400 bg-white rounded-md shadow-sm focus:outline-none focus:ring-indigo-500 focus:border-indigo-500 sm:text-sm ">
                </div>
                <div class="flex flex-row items-center my-3 mx-2">
                    <label class="uppercase block text-sm font-medium text-gray-700 mx-2 w-32 rounded-md px-1">Total en almacen</label>
                    <input v-model="formData.stock" type="text" class="flex-1 border mt-1 block w-full py-2 px-3 border-gray-400 bg-white rounded-md shadow-sm focus:outline-none focus:ring-indigo-500 focus:border-indigo-500 sm:text-sm ">
                    
                </div>
                <div class="flex flex-row items-center my-3 mx-2">
                    <label class="uppercase block text-sm font-medium text-gray-700 mx-2 w-32 rounded-md px-1">Precio *</label>
                    <input v-model="formData.price" type="text" class="flex-1 border mt-1 block w-full py-2 px-3 border-gray-400 bg-white rounded-md shadow-sm focus:outline-none focus:ring-indigo-500 focus:border-indigo-500 sm:text-sm ">
                </div>
                <div class="flex flex-row items-center my-3 mx-2">
                    <label class="uppercase block text-sm font-medium text-gray-700 mx-2 w-32 rounded-md px-1">Características</label>
                    <div class="flex flex-wrap gap-3 w-full my-3 px-3 py-2 border-double border border-gray-400 rounded-lg">                    
                        <button type="button" 
                            class="mb-auto ml-auto border border-gray-400 bg-lightBlue rounded-md font-semibold text-xs text-white uppercase tracking-widest shadow-sm p-1 px-3"
                            @click="addFeature"><i class="fa fa-plus"> Agregar Característica</i>
                        </button>
                        <Features 
                        v-for="(feature, index) in formData.features"
                        :key="index"
                        :info="feature"
                        :index="index"
                        @delete="delFeature"
                        @update="updFeature"
                        />                                                                        
                    </div>
                </div>
                <div class="flex flex-row items-center my-3 mx-2">
                    <label class="uppercase block text-sm font-medium text-gray-700 mx-2 w-32 rounded-md px-1">Imagen</label>
                    <add-images :index="1" :info="img" v-on:imgStatus="updImage"
                    class="flex-1 mt-1 block w-full bg-white focus:outline-none focus:ring-indigo-500 focus:border-indigo-500 sm:text-sm "></add-images>
                </div>

            </div>            

            <!-- Button -->
            <div class="flex flex-1 gap-3 mt-10 flex-row-reverse gap-y-4">
                <button type="button" class="inline-flex border border-transparent  bg-green-600 rounded-md font-semibold text-xs text-white uppercase tracking-widest px-4 py-2 
                    hover:bg-green-500 focus:outline-none 
                    focus:border-green-700 focus:ring focus:ring-green-200
                    active:bg-green-600 
                    disabled:opacity-25 transition"
                    :disabled="load"
                    @click="submitForm"
                    >
                    Salvar
                    <span :class="{'hidden': !load}"><i class="fa fa-spinner fa-pulse"></i></span>
                </button>            
                <button type="button" class="inline-flex border border-gray-400 bg-red rounded-md font-semibold text-xs text-white uppercase tracking-widest shadow-sm px-4 py-2
                    hover:text-white 
                    focus:outline-none focus:border-red-300 focus:ring focus:ring-red-200 
                    active:text-gray-800 active:bg-red-50 
                    disabled:opacity-25 transition"
                    :disabled="load"
                    >Cancelar
                </button> 
                <div v-if="error_message" class="error-message">
                    <i class="fa fa-times-circle"></i>
                    <h5 class="text-xl self-center px-2">{{error_message_text}}</h5>
                  </div>
                  <div v-if="success_message" class="success-message">
                    <i class="fa fa-check-circle"></i>
                    <h5 class="text-xl self-center px-2">{{success_message_text}}</h5>
                </div>                           
            </div>

        </form>
    </div>
</template>
<script>

import MarketplaceItemService from "@/services/marketplace/marketplace_item.service";
import MarketplaceCategoryService from "@/services/marketplace/marketplace_category.service";
import Features from "./partials/Features";


import { MultiSelect } from 'vue-search-select';
import AddImages from '@/components/AddImages';

export default {
    props: {
        info: {},
        mode: {
            default: 'create'
        }
    },    
    name: "CrudMarketplaceItem",
    components: {
        AddImages,
        MultiSelect,
        Features,
    },
    data() {
        return {
            success_message_text: 'La información ha sido enviada satisfactoriamente.',
            error_message_text: '',
            error_message: false,
            success_message: false,
            categoriesSelected: [],
            load: false,
            img: {},
            features: [], 
            formData: {
                //general info
                // id: '',
                alias: '',
                keyword: '',
                description: '',
                discount: 0,
                stock: 0,
                price: '',
                features: [],
                images: [],
                categories: [],
                active: true,
            },
        }
    },
    created() {
        MarketplaceCategoryService.getSimpleList().then(item => { this.categoriesSelected = item })
        this.initForm() 
    },
    watch: {
        mode() {            
            this.initForm()
        },                     
    },
    computed: {
        is_admin() {
            return Authorization.validateRole()
        }
    },        
    methods:{
        initForm() {
            // For upd action
            MarketplaceItemService.getMarketplaceItem(this.info)
            .then(item => {
                if(this.mode != 'create') {
                    this.formData.id = item.id
                    this.formData.alias = item.alias
                    this.formData.description = item.description
                    this.formData.keyword = item.keyword
                    this.formData.discount = item.discount
                    this.formData.stock = item.stock
                    this.formData.price = item.price
                    this.formData.features = item.features
                    this.formData.active = item.active
                    this.formData.categories = [];                    

                    // console.log(this.formData.features);
                    // categorias
                    if(item.marketplace_categories.length>0)
                        item.marketplace_categories.forEach(el => this.formData.categories.push(
                            {
                                value: el.id,
                                text: el.alias,
                            }
                        ))
                    // console.log(this.formData.categories)
                    // imagen
                    if(item.images)    
                        this.initGallery(item.images)
                }
            })            
        },
        validate(value) {
            if (value == '')
                return false
            else if(value === undefined)
                return false
            else if(value === null)
                return false
            return true
        },
        updImage(obj) {
            if(obj.index >= 0) {
                this.img = obj.img
                this.img.id_object_class = (this.mode == 'create') ? 0 : this.formData.id
            }
        },
        initGallery(images) {
            // this.galleries = []
            for(let img of images) {
                const original = img.resources.filter(i => i.image == 'original')                
                this.img = {
                    id: img.id,
                    class_name: img.class_name,            
                    id_object_class: img.id_object_class,
                    subcategory: img.subcategory,
                    lang: img.lang,
                    image_name: img.image_keyword,
                    image_keyword: img.image_keyword,
                    url: original[0].url,
                    file: {},
                    ext: img.ext,
                    anno: img.anno,
                    order: img.order,
                    active: img.active,
                }
            }
            
        },        
        submitForm(e) {            
            e.preventDefault();
            this.error_message = false
            this.success_message = true
            
            if(!this.validate(this.formData.alias) ||
            //    !this.validate(this.formData.description) ||
               !this.validate(this.formData.keyword) ||
            //    !this.validate(this.formData.margin) ||
               !this.validate(this.formData.price) ||
            //    !this.validate(this.formData.features) ||
               !this.formData.categories.length>0
            ){
                this.error_message = true
                this.success_message = false 
                this.error_message_text = 'Faltan datos por llenar'
            }
            const data = {                                        
                    alias: this.formData.alias,
                    description: this.formData.description,
                    active: (this.formData.active) ? 1 : 0,
                    keyword: this.formData.keyword,
                    discount: this.formData.discount ? this.formData.discount : 0,
                    stock: this.formData.stock ? this.formData.stock : 0,
                    price: this.formData.price,
                    features: this.formData.features,
            }

            data.categories = this.formData.categories.map(el => el.value)
            
            // Unir las features en un solo texto
            data.features = data.features.map(el => el.title + ': ' + el.value).join('/*/')

            if('id' in this.formData) data.id = this.formData.id
            const fd = new FormData()  
            
            if(!!this.img.url){
                if(this.img.url.includes('blob')) {                    
                    fd.append('images', this.img.file, this.img.file.name)
                } else 
                    fd.append('images', this.img.id)
                fd.append('imgOrder', [1])
                fd.append('subcategory', [this.img.subcategory])
                // agrego los otros campos
            }
            
            for(let field of Object.keys(data)) {
                fd.append(field, data[field])
            }
            
            if(!this.error_message)
            {                   
                this.load = true                
                MarketplaceItemService.addUpdMarketplaceItem({form: fd, id: data.id}, (this.mode === 'create'))
                .then(res => {
                    this.load = false
                    if(res) {
                        this.error_message = false
                        this.success_message = true                  
                        setTimeout(() => {                            
                            this.success_message = false
                            this.initForm()
                        }, 3000); 
                    } else {
                        this.error_message = true
                        this.success_message = false
                        this.error_message_text = 'Error creando el producto'
                        setTimeout(() => {
                            this.error_message = false
                        }, 3000);    
                    }
                })
            }
        },
        async getMarketplaceItem(e) {
            console.log(e)
        },
        onCategorySelect(items, litem){
            this.formData.categories = items
            // console.log(items)
        },
        // Features
        addFeature() {
            this.formData.features.push({ title: '', value: '' })
        },
        delFeature(index) {
            this.formData.features.splice(index, 1)
        },
        updFeature({ index, title, value }) {
            this.formData.features[index] = { title, value }
        }
    }
}
</script>
